import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { StyledList, StyledListItem } from '../../people/peopleList/peopleList.styled';
import { fetchPlanets, selectPlanet,setDefaultBroken } from "../../../actions";
import NoSSR from '@mpth/react-no-ssr';
import Spinner from '../../spinner/spinner';


const PlanetList = ({ fetchPlanets, selectPlanet, planets, isFetchingPlanet, setDefaultBroken }) => {
  useEffect(() => {
    fetchPlanets()
  }, [])

  return (
    <NoSSR>
      <StyledList>
        {isFetchingPlanet ? <Spinner /> :
          <>
            {
              planets.map(el =>
                <StyledListItem onClick={() => selectPlanet(el.name), setDefaultBroken()}>{el.name}</StyledListItem>
              )
            }
          </>
        }
      </StyledList>
    </NoSSR>
  )
}

const mapStateToProps = (state) => {
  return {
    planets: state.planets,
    isFetchingPlanet: state.isFetchingPlanet
  }
}



export default connect(mapStateToProps, { fetchPlanets, selectPlanet,setDefaultBroken })(PlanetList);